<template>
  <div id="app">
    <div id="footer" v-show="footerFlag">
      <li>
        <router-link to="/"
          ><van-icon size="25" name="wap-home-o" />首页</router-link
        >
      </li>
      <li>
        <router-link to="/about"
          ><van-icon size="25" name="apps-o" />分类</router-link
        >
      </li>
      <li>
        <router-link to="/Find"
          ><van-icon size="25" name="eye-o" />发现</router-link
        >
      </li>
      <li>
        <router-link to="/My"
          ><van-icon size="25" name="user-o" />我的</router-link
        >
      </li>
    </div>

    <!-- <van-pull-refresh v-model="refreshing" @refresh="onRefresh"> -->
      <van-list v-model="loading" :finished="finished" @load="onLoad">
        <keep-alive>
          <router-view />
        </keep-alive>
      </van-list>
    <!-- </van-pull-refresh> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
    };
  },
  methods: {
    onLoad() {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = [];
          this.refreshing = false;
        }

        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        this.loading = false;

        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
  },
  computed:{
    footerFlag(){
      return this.$store.state.footerFlag;
    }
  },
};
</script>

