      <template #title>
        <div
          @click="toggle"
          :class="{ father: true, light: topToggle || scrollFlag }"
          v-show="!topToggle"
        >
          <span :class="{ defalt: !topToggle, fatherspan: scrollFlag }"
            >推荐</span
          >
          <span :class="{ active: topToggle }">更新</span>
        </div>

        <div
          @click="toggle"
          :class="{ father: true, light: topToggle || scrollFlag }"
          v-show="topToggle"
        >
          <span :class="{ actives: topToggle }">推荐</span>
          <span :class="{ active: topToggle }">更新</span>
        </div>
      </template>

      <template #right>
        <span
          class="iconfont icon-sousuo"
          :class="{ rightspan: topToggle }"
        ></span>
        <i :class="{ righti: topToggle }"></i>
      </template>
    </van-nav-bar>

    <div class="tuijian" v-show="!topToggle">
      <div class="banner">
        <keep-alive>
          <van-swipe @change="onChange" :autoplay="3000" loop>
            <van-swipe-item v-for="(image, index) in images" :key="index"
              ><img :src="image" />
            </van-swipe-item>
            <template #indicator>
              <div class="custom-indicator">
                <van-icon
                  name="thumb-circle"
                  v-for="item in 6"
                  :key="item"
                  :style="{
                    color: item != current + 1 ? 'rgba(0, 0, 0, 0.8)' : 'white',
                  }"
                />
              </div>
            </template>
          </van-swipe>
        </keep-alive>
        <div class="img">
          <img
            src="http://css99tel.cdndm5.com/v202012291638/blue/images/mobile/banner-bottom.png"
            alt=""
          />
        </div>
