<script>
import BoxListOne from "@/components/BoxListOne.vue";
// import BoxListTwo from "../components/BoxListTwo.vue";
import BoxListThree from "../components/BoxListThree.vue";
import Update from "../components/Update.vue";

export default {
  components: {
    BoxListOne,
    // BoxListTwo,
    BoxListThree,
    Update,
  },
  data() {
    return {
      topToggle: false,
      images: [
        "https://image.zymkcdn.com/file/news/000/004/391.jpg",
        "https://image.zymkcdn.com/file/news/000/004/450.jpg",
        "https://image.zymkcdn.com/file/news/000/004/425.jpg",
        "https://image.zymkcdn.com/file/news/000/004/423.jpg",
        "https://image.zymkcdn.com/file/news/000/004/396.jpg",
        "https://image.zymkcdn.com/file/news/000/004/387.jpg",
      ],
      bannerlist: [],
      current: 0,
      scroll: 0,
      scrollFlag: false,
    };
  },
  created() {
    this.$store.commit("modifyID");
    window.onscroll = () => {
      this.scroll = document.documentElement.scrollTop;
      if (this.topToggle) {
        this.scrollFlag = true;
      } else {
        if (this.scroll >= 230) {
          this.scrollFlag = true;
        } else if (this.scroll == 0) {
          this.scrollFlag = false;
        }
      }
    };
  },
  methods: {
    insertStr(soure, start, newStr) {
      return soure.slice(0, start) + newStr + soure.slice(start);
    },
    toggle() {
      this.topToggle = !this.topToggle;
      var fatherSpan = document.querySelectorAll(".father span");
      if (this.topToggle) {
        this.scrollFlag = true;
        fatherSpan[0].style.color = "white";
      } else {
        this.scrollFlag = false;
        fatherSpan[0].style.color = "rgba(0, 0, 0, 0.8)";
      }
      this.scroll = document.documentElement.scrollTop;
      if (this.scroll >= 230) {
        this.scrollFlag = true;
      } else if (this.scroll == 0) {
        this.scrollFlag = false;
      }
    },
    onChange(index) {
      this.current = index;
    },
  },
  computed: {
    homeBook() {
      return this.$store.state.homeBook;
    },
  },
};
</script>

