<template>
  <div class="three">
    <div class="head">
      <p>{{ item.title }}</p>
      <span><van-icon name="weapp-nav" />更多</span>
    </div>
    <div class="body">
      <van-swipe :show-indicators="false">
        <van-swipe-item>
          <van-grid :border="false" :column-num="2">
            <van-grid-item
              v-for="items in homeBook[index].comic_info"
              :key="items.comic_id"
              @click="jump(items)"
            >
              <van-image
                :src="
                  'https://image.zymkcdn.com/file/cover/' +
                  items.comic_id +
                  '_2_1.jpg-720x360.jpg.webp'
                "
              />
              <div class="fonts">
                <span class="left">{{ items.comic_type[0] }}</span>
                <span class="right">{{ items.comic_name }}</span>
              </div>
              <div class="hot-black">
                <van-icon name="fire-o" /><span>{{
                  items.total_view_num
                }}</span>
              </div>
              <p>{{ items.content }}</p>
            </van-grid-item>
          </van-grid>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script>
export default {
  props:['item','index'],
  computed:{
    homeBook(){
      return this.$store.state.homeBook;
    }
  },
  methods: {
    jump(event){
      this.$store.commit("falseFooterFlag");
      this.$router.push({path:'/ComicsList',query:{id:event}});
    }
  },
};
</script>

