<template>
  <div class="one">
    <div class="head">
      <p>{{ item.title }}</p>
      <span><van-icon name="weapp-nav" />更多</span>
    </div>
    <div class="body">
      <van-swipe :show-indicators="false">
        <!-- 第一个轮播图页面 -->
        <van-swipe-item>
          <van-grid :border="false" :column-num="2">
            <van-grid-item
              v-for="items in homeBook[index].comic_info.slice(
                0,
                homeBook[index].comic_info.length / 3
              )"
              :key="items.comic_id"
              @click="jump(items)"
            >
              <van-image
                :src="
                  'https://image.zymkcdn.com/file/cover/' +
                  items.comic_id +
                  '_2_1.jpg-720x360.jpg.webp'
                "
              />
              <div class="fonts">
                <span class="left">{{ items.comic_type[0] }}</span>
                <span class="right">{{ items.comic_name }}</span>
              </div>
              <div class="hot-black">
                <van-icon name="fire-o" /><span>{{
                  items.total_view_num
                }}</span>
              </div>
              <p>{{ items.content }}</p>
            </van-grid-item>
          </van-grid>
        </van-swipe-item>
        <!-- 第二个轮播图页面 -->
        <van-swipe-item>
          <van-grid :border="false" :column-num="2">
            <van-grid-item
              v-for="items in homeBook[index].comic_info.slice(
                homeBook[index].comic_info.length / 3,
                (homeBook[index].comic_info.length / 3) * 2
              )"
              :key="items.comic_id"
              @click="jump(items)"
            >
              <van-image
                :src="
                  'https://image.zymkcdn.com/file/cover/' +
                  items.comic_id +
                  '_2_1.jpg-720x360.jpg.webp'
                "
              />
              <div class="fonts">
                <span class="left">{{ items.comic_type[0] }}</span>
                <span class="right">{{ items.comic_name }}</span>
              </div>
              <div class="hot-black">
                <van-icon name="fire-o" /><span>{{
                  items.total_view_num
                }}</span>
              </div>
              <p>{{ items.content }}</p>
            </van-grid-item>
          </van-grid>
        </van-swipe-item>
        <!-- 第三个轮播图页面 -->
        <van-swipe-item>
          <van-grid :border="false" :column-num="2">
            <van-grid-item
              v-for="items in homeBook[index].comic_info.slice(
                (homeBook[index].comic_info.length / 3) * 2,
                homeBook[index].comic_info.length
              )"
              :key="items.comic_id"
              @click="jump(items)"
            >
              <van-image
                :src="
                  'https://image.zymkcdn.com/file/cover/' +
                  items.comic_id +
                  '_2_1.jpg-720x360.jpg.webp'
                "
              />
              <div class="fonts">
                <span class="left">{{ items.comic_type[0] }}</span>
                <span class="right">{{ items.comic_name }}</span>
              </div>
              <div class="hot-black">
                <van-icon name="fire-o" /><span>{{
                  items.total_view_num
                }}</span>
              </div>
              <p>{{ items.content }}</p>
            </van-grid-item>
          </van-grid>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script>
export default {
  props:['item','index'],
  computed:{
    homeBook(){
      return this.$store.state.homeBook;
    },
  },
  methods: {
    jump(event){
      this.$store.commit("falseFooterFlag");
      this.$router.push({path:'/ComicsList',query:{id:event}});
    }
  },
};
</script>

