<template>
  <div class="update">
    <div class="banner">
      <keep-alive>
      </keep-alive>
    </div>
    <van-sticky :offset-top="51">
      <div class="day">
        <p
          v-for="(item, index) in comicList"
          :key="index"
          @click="toggleDay(index)"
          :class="{ now: pNow == index }"
        >
          {{ item.comicUpdateDate }}
        </p>
        <img :src="down" alt="" />
      </div>
    </van-sticky>
    <div class="boxes">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <van-cell v-for="item in comicList[pNow].info" :key="item.comic_idOld" @click="jump(item)">
          <div class="img">
            <img
              :src="
                'https://image.zymkcdn.com/file/cover/' +
                item.comic_id +
                '_2_1.jpg-720x360.jpg.webp'
              "
              alt=""
            />
            <div class="hot"><van-icon name="fire-o" />{{ item.hot }}</div>
          </div>
          <div class="fonts">
            <div class="left">
              <p class="big">
                <b>{{ item.comic_name }}</b>
                <span>{{ item.comic_type[0].name }}</span>
              </p>
              <p>{{ item.comic_feature }}</p>
            </div>
            <div class="right">
              <p>{{ item.author_name }}</p>
              <p>
                更新至<span>{{ item.last_chapter.name }}</span>
              </p>
            </div>
          </div>
        </van-cell>
      </van-list>
    </div>
  </div>
</template>

<script>
import down from "../assets/369.png";
export default {
  data() {
    return {
      pNow: 6,
      down: down,
      loading: false,
      finished: false,
    };
  },
  computed: {
    comicList() {
      return this.$store.state.comicList;
    },
  },
  created() {
    this.$store.commit("comicListID");
  },
  methods: {
    jump(event){
      this.$store.commit("falseFooterFlag");
      this.$router.push({path:'/ComicsList',query:{id:event}});
    },
    onChange(index) {
      this.current = index;
    },
    toggleDay(index) {
      this.pNow = index;
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
  },
};
</script>

<style lang='less'>
.update {
  padding-top: 51px;
  .day {
    display: flex;
    width: 100%;
    justify-content: space-around;
    position: relative;
    background: white;
    padding: 10px 0;
    img {
      position: absolute;
      bottom: -12px;
      left: 0;
      z-index: 999;
    }
    p {
      width: 25px;
      height: 25px;
      text-align: center;
      line-height: 25px;
      border-radius: 50%;
      font-size: 14px;
    }
    .now {
      background-color: #fd8b7f;
      color: white;
    }
  }
  .boxes {
    .van-cell {
      padding: 0;
      .img {
        position: relative;
        .hot {
          position: absolute;
          bottom: 20px;
          right: 10px;
          color: rgba(255, 255, 255, 0.85);
          background: rgba(0, 0, 0, 0.5);
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 0px 8px;
          border-radius: 10px;
          font-size: 12px;
          i{
            margin-right: 3px;
          }
        }
      }
      .fonts {
        width: 100%;
        display: flex;
        justify-content: space-between;
        color: #999;
        font-size: 12px;
        padding: 0 10px;
        .left {
          width: 50%;
          p {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
          .big {
            font-size: 18px;
            color: black;
            display: flex;
            align-items: center;
            b {
              font-weight: normal;
              display: inline-block;
              max-width: calc(100% - 43px);
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
            }
            span {
              margin: 0 0 0 5px;
              font-size: 12px;
              color: #f7b84e;
              display: inline-block;
              padding: 0px 6px;
              line-height: 16px;
              border-radius: 10px;
              border: 1px solid #f7b84e;
            }
          }
        }
        .right {
          width: 50%;
          text-align: right;
          p {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
          span {
            color: #fd8b7f;
          }
        }
      }
    }
  }
}
</style>